.card {
  display: flex;
  flex-flow: column nowrap;
  width: 180px;
  min-height: 240px;
  background-color: #7673d980;
  border: 2px solid #1F1AB2;
  border-radius: 5px;
  padding: 10px;

  & > p {
    margin-bottom: 10px;
  }

  .title {
    margin-right: 5px;
    font-weight: bold;
    color: #05316D;
  }
}
