.form {
  margin: 10px auto;
  width: 100%;
  max-width: 500px;
  background-color: #7673d980;
  border: 3px solid #1F1AB2;
  border-radius: 10px;
  padding: 5px;
  display: flex;
  flex-flow: column nowrap;
}

.subheading {
  margin: 10px;
  text-align: center;
}

.btn {
  width: 140px;
  height: 40px;
  align-self: center;

  input {
    width: 100%;
    height: 100%;
    font-size: 22px;
    background-color: #009B95;
    border: none;
    border-radius: 4px;

    &:disabled {
      background-color: #284C7E;
    }

    &:not(:disabled):hover {
      cursor: pointer;
      background-color: #33CDC7;
    }

    &:not(:disabled):active {
      background-color: #1D7471;
    }
  }
}

.item {
  display: block;
  margin: 10px;
  position: relative;

  input:not([type="radio"]),
  input[type="text"]:valid,
  select {
    background-color: #a0dddb;
    border: 1px solid #05316D;
    border-radius: 2px;
    padding: 0 5px;
  }

  input {
    &[type="text"],
    &[type="date"] {
      width: 100%; 
      height: 25px;
    }

    &[type="checkbox"] {
      margin-right: 5px;
      height: 20px;
      width: 20px;
      appearance: none;
      border: 1px solid #05316D;
      border-radius: 2px;
      outline: none;
      transition-duration: 0.3s;
      background-color: #a0dddb;
      cursor: pointer;

      & + .checkboxLabel {
        cursor: pointer;
      }

      &:checked + .checkboxLabel::before {
        content: 'âœ•';
        display: block;
        position: absolute;
        top: 0;
        left: 3px;
        width: 20px;
        height: 20px;
        font-size: 20px;
        line-height: 20px;
        border-radius: 2px;
        color: #1F1AB2;
      }
    }
  }

  select {
    width: 100%; 
    height: 25px;
  }
}

.switch {
  position: relative;
  width: 100%;
  height: 25px;

  span {
    margin-right: 5px;
    position: relative;
    bottom: 5px;
  }
  
  #switch1,
  #switch2, 
  #switch3 {
    position: absolute;
    z-index:9;
    -webkit-appearance: none;
    
    & + label {
      position: absolute;
      text-align: center;
      vertical-align: middle;
      line-height: 25px;
      width: 60px;
      height: 25px;
      z-index: 11;

      &::after {
        position: absolute;
        content: "";
        width: 60px;
        height: 25px;
        top: 0;
        left: 0;
        z-index: 10;
        cursor: pointer;
      }
    }
  }
  #switch1 {
    & + label {
      left: 35px;
    }
    &:checked {
      & ~ .switchInner {
        &::after {
          left: 35px;
          transition: left 200ms ease-in-out;
        }
      }
    }
  }
  #switch2 {
    & + label{
      left: 95px;
    }
    &:checked {
      & ~ .switchInner {
        &::after {
          left: 95px;
          transition: left 200ms ease-in-out;
        }
      }
    }
  }
  #switch3 {
    & + label {
      left: 155px;
    }
    &:checked {
      & ~ .switchInner {
        &::after {
          left: 155px;
          transition: left 200ms ease-in-out;
        }
      }
    }
  }

  &Inner {
    display: inline-block;
    width: 180px;
    height: 25px;
    background-color: #a0dddb;
    border: 1px solid #05316D;
    border-radius: 2px;
    
    &::after {
      position: absolute;
      display: block;
      content:"";
      width: 60px;
      height: 23px;
      top: 0;
      left: 0;
      background-color: #33CDC7;
      border: solid 1px #006561;
      border-radius: 4px;
      transition: left 200ms ease-in-out;
    }
  }
}
