.header {
  min-height: 120px;
  padding: 20px 10px;
  background-color: #4380D3;
  display: flex;
  flex-flow: column nowrap;
}

.heading {
  margin: 0 auto;
  font-size: 40px;
  color: 	#7673D9;
  -webkit-text-stroke: 1px #0C0874;
  font-family: 'Press Start 2P', cursive;
  text-shadow:
       2px 2px 0 #0C0874,
     -1px -1px 0 #0C0874,  
      1px -1px 0 #0C0874,
      -1px 1px 0 #0C0874,
       1px 1px 0 #0C0874;
  text-transform: uppercase;
}

.description {
  margin-top: 10px;
  font-family: 'Caveat', cursive;
  font-size: 20px;
  color: #00413e;
  align-self: flex-end;
}
